
















	export default {
		data() {
			return {
				positionTop: 0,
			};
		},
		methods: {
			goPhoneLogin() {
				uni.navigateTo({
					url: '../login-phone/login-phone'
				})
			},
			initPosition() {
				/**
				* 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
				* 反向使用 top 进行定位，可以避免此问题。
				*/
				this.positionTop = uni.getSystemInfoSync().windowHeight - 70;
			},
			goBack() {
				uni.switchTab({
					url: '../../index/index'
				});
			},
			bindLogin() {
				// console.log(res.provider)
				uni.login({
					provider: 'weixin',
					success: function (loginRes) {
						// console.log(JSON.stringify(loginRes.authResult));
						// 获取用户信息
						uni.getUserInfo({
						    provider: 'weixin',
						    success: function (infoRes) {
								// console.log(JSON.stringify(infoRes.userInfo));
								uni.request({
									url: 'http://www.aikm.cn/api/login',
									data: {
										judge: 2,
										unionid: infoRes.userInfo.unionId,
										app_open_id: infoRes.userInfo.openId,
										nickname: infoRes.userInfo.nickName,
										head_img: infoRes.userInfo.avatarUrl,
										type: 'android',
										sex: infoRes.userInfo.gender
									},
									method: 'POST',
									success: (res) => {
										// console.log(res);
										// console.log(JSON.stringify(res.data));
										// this.text = 'request success';
// 										var loginData = {
// 											hasLogin: true,
// 											userData: res.data.data
// 										}
										if(res.data.code == 1) {
											uni.setStorage({
												key: 'userData',
												data: res.data.data,
												success: function () {
													console.log('success');
												}
											});
											uni.setStorage({
												key: 'hasLogin',
												data: true,
												success: function () {
													console.log('success');
												}
											});
											uni.switchTab({
												url: '../../index/index',
											});
										}
									}
								});
						    }
						});
						
					}
				});
			}
		},
		onLoad() {
			this.initPosition();
		}
	}



